* {
  margin: 0;
  padding: 0;
  ::-webkit-scrollbar {
    display: none;
  }
}
li {
  list-style-type: none;
}

@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap");

.navbar {
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid #e5e5e5;
  padding: 5px;
  position: sticky;
  top: 0;
  background-color: white;
  z-index: 100;
  .left {
    display: flex;
    .menu {
      padding: 15px;
      display: flex;
      align-items: center;
    }
    .logo {
      display: flex;
      align-items: center;
      img {
        width: 30px;
      }
      h5 {
        font-family: "Roboto";
        font-weight: 500;
        margin-left: 2px;
      }
    }
  }
  .middle {
    display: none;
  }
  .right {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    svg {
      margin-right: 10px;
    }
    .hide {
      display: none;
    }
  }
}
.topics {
  display: flex;
  padding: 10px 0px 10px 10px;
  overflow-x: scroll;
  border-bottom: 1px solid #e5e5e5;

  .butt {
    display: flex;
    .buttons {
      padding: 5px 10px;
      color: black;
      border-radius: 16px;
      border: 1px solid gray;
      margin-right: 4px;
      background-color: rgb(230, 230, 230);
      cursor: pointer;
      h6 {
        font-size: 14px;
      }
    }
    .buttons:hover {
      background-color: #909090;
      transition: 100ms ease-in-out;
    }
  }
}
.main-page {
  display: flex;
  .sidebar {
    flex: 0.2;
    // position: sticky;
    // top: 0;
    // background-color: white;
    // z-index: 10;
    // overflow-y: scroll;
    hr {
      height: 1px;
      border: 0;
      background-color: lightgray;
      margin-top: 10px;
      margin-bottom: 10px;
    }
    .sidebarRow {
      display: flex;
      align-items: center;
      padding: 10px 20px;

      .icon_row {
        color: grey;
        font-size: large !important;
      }
      .title_row {
        flex: 1;
        margin-left: 20px;
        font-size: 16px;
        font-weight: 500;
      }
    }
    .sidebarRow:hover {
      background-color: lightgray;
      cursor: pointer;
      .icon_row {
        color: red;
      }
      .title_row {
        font-weight: bold;
      }
    }
    .sidebarRow.selected {
      background-color: lightgray;
      .icon_row {
        color: red;
      }
      .title_row {
        font-weight: bold;
      }
    }
  }
  .main {
    flex: 0.8;
    background-color: #f9f9f9;
    padding: 40px 20px;

    .recommendedVideos__videos {
      display: flex;
      flex-wrap: wrap;
      .videocard {
        width: 270px;
        margin-bottom: 40px ;
        .videocard__thumbnail{
          height: 140px;
          width: 250px;
        }
        .videoCard__info{
          .video__text{
           
          }
        }
      }
    }
  }
}

@media (min-width: 496px) {
  .navbar {
    .middle {
      display: flex;
      align-items: center;
      border: 1px solid black;
      height: fit-content;
      svg {
        height: 46px;
      }
      input {
        border: none;
        outline: none;
        padding: 15px;
        width: 350px;
      }
      .search-click {
        width: 20px !important;
        background-color: #fafafa;
        border-left: 1px solid lightgray;
        color: gray;
      }
    }
    .right {
      .hide {
        display: block;
      }
    }
  }
}

